button,
.button {
    text-decoration: none;
    transition: all 1s;
    cursor: pointer;
    display: inline-block;
    white-space: nowrap;
    font-size: 2.4rem;
    position: relative;
    border: 2px solid;
    border-right: 0;
    background: $cp1;
    background: linear-gradient(90deg, $cp1 0%, $cp1 50%, $cp1 50%, $cp2 50%, $cp2 100%);
    background-size: 207% 100%;
    background-position: 99% 0;
    outline: 0;
    padding: 1em;
    line-height: 1.15;
    margin-right: 1em;

    &:before {
        width: 2em;
        height: 2em;
        position: absolute;
        top: -2.05em;
        right: -1.95em;
        background-color: $cp2;
        content: "";
        transform: rotate(57deg);
        transition: 1s all;
        border-bottom: 2px solid $cp1;
        transform-origin: bottom left;
    }

    &:after {
        width: 1.9em;
        height: 1.9em;
        position: absolute;
        bottom: -0.03em;
        right: -1.9em;
        background-color: $cp2;
        content: "";
        transform: rotate(33deg);
        transition: 1s all;
        border-left: 2px solid $cp1;
        transform-origin: bottom left;
    }

    &.small {
        font-size: 1.4rem;

        &:before {
            width: 2em;
            height: 2em;
            top: -2.25em;
        }

        &:after {
            width: 2em;
            height: 2em;
            top: 1.2em;
        }
    }

    &:hover {
        color: $cp2;
        background-position: 0% 0;
        padding: 1em 2em 1em 1em;
        margin-right: 0;

        &:before {
            right: -0.95em;
            top: -1.95em;
        }

        &:after {
            right: -0.83em;
            bottom: 0.05em;
        }
    }

    &.disabled,
    &[disabled] {
        pointer-events: none;
        opacity: 0.35;
    }
}



